import crop from '../../components/crop.vue'

export default {
	data() {
		return {
			checks: [],
			id: 0,
			no: "",
			look: false,
			asset: null,
			requestUrl: null,
			photoSrc: "",
			ccid: 0
		}
	},
	// onBackPress: function () {
	// 	if (this.$refs.crop.isShow) {
	// 		this.$refs.crop.isShow = false
	// 	}
	// 	return true;
	// },
	components: {
		crop
	},
	onLoad: function (options) {
		this.id = options.id
		getApp().sendSocket("getCheckList", {id: options.id})
		this.requestUrl = getApp().globalData.requestUrl
	},
	onShow: function() {
		uni.onSocketMessage((res) => {
			this.getMessage(JSON.parse(res.data))
		})
	},
	methods: {
		getMessage: function(data) {
			uni.hideLoading()
			let msg = data.msg
			let obj = data.obj
			if (msg == "checks") {
				this.checks = obj
			} else if (msg == "assetInfo") {
				if (this.no == obj.as_no) {
					this.asset = obj
					this.look = true
				} else {
					uni.showToast({
						title: "资产与二维码不符"
					})
				}
			} else if (msg == "checkSuccess") {
				uni.showToast({
					icon: "success",
					title: "盘点成功"
				})
				getApp().sendSocket("getCheckList", {id: this.id})
			}
		},
		check: function (e) {
			let no = e.currentTarget.dataset.no
			let ccid = e.currentTarget.dataset.ccid
			this.no = no
			this.ccid = ccid
			uni.scanCode({
				onlyFromCamera: true,
				success: (res) => {
					if (res.result.length < 100) {
						uni.showToast({
							title: "扫码失败请重试"
						})
					} else {
						this.checkQrcode(res.result)
					}
				}
			})
			// this.checkQrcode('')
		},
		checkQrcode: function (res) {
			getApp().sendSocket("getAssetInfo", {no: res})
		},
		start: function () {
			this.look = false
			uni.chooseImage({
				count: 1,
				sizeType: ["compressed"],
				// sourceType: ['camera'],
				success: res => {
					let path = res.tempFilePaths[0]
					this.photoSrc = path
					this.$refs.crop.isShow = true
					// this.assetInfo.as_image = path
				}
			})
		},
		uploadF: function (e) {
			getApp().uploadF(e, res => {
				this.checkAsset(res.data)
			})
		},
		checkAsset: function (res) {
			getApp().sendSocket("checkAsset", {
				ccid: this.ccid,
				cid: this.id,
				assetImage: this.asset.as_image,
				newImage: res
			})
		}
	}
}
